import React, { PropTypes } from 'react'
import { ViewPager, Frame, Track, View } from 'react-view-pager'


class Offer extends React.Component {
    constructor(props) {
        super(props);
    }




    render() {
        return (
            <View>
            <div className="sr-offer__container" style={{border: "2px solid " + this.props.color, boxShadow: "0 0 10px" + this.props.color}}>
                <div >
                    <text className="sr-offer__title" style={{color: this.props.color}} >{this.props.title}</text>
                </div>
                <br></br>
                <div >
                    <text className="sr-offer__type">{this.props.type}</text>
                </div>
                <br></br>
                <div>
                    <text className="sr-offer__payment">{this.props.payment}</text>
                </div>
                <br></br>
                <div >
                    <text className="sr-offer__price">{this.props.price}</text>
                </div>
                <div >
                    <img className="sr-offer__image" src={this.props.image}></img>
                </div>
            </div>
</View>
        )
    }
}

export default Offer



