import React from 'react'
import { ViewPager, Frame, Track, View } from 'react-view-pager'

export default class Product extends React.Component {
  constructor(props) {
    super(props)
  }

  handleOnMouseUp() {
    this.props.clickMethod(this.props.link)
  }

  render() {
    var quoteClass =  this.props.index == this.props.indice ? "quote-box-blue" : "quote-box-white"
    var titleClass =  this.props.index == this.props.indice ? "sr-home__title__white" : "sr-home__title__blue"

    return (
      <View>
      <div className={"quote-box " + quoteClass} >
        <div className={"title " + titleClass}>
          {this.props.title}
        </div>
        <a href="javascript:void(0)"onMouseUp={this.handleOnMouseUp.bind(this)}>
        <img className="sr-home__icon " style={{ width: this.props.width }} src={this.props.index == this.props.indice ? this.props.icon_selected : this.props.icon}></img>
      </a>
      </div>
      </View>
    )
  }
}



