import React, {PropTypes} from 'react'
import Translations from '../../services/translations';
import CurrentLocal from '../../services/currentLocal'
import Connector from '../../utils/connector'
import { Route, Redirect } from 'react-router'

class Binary extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      response: null,
      option_id: null,
      quizz: {
        name: "",
        description: ""
      },
      redirect: '',
    }
    this.renderImage = this.renderImage.bind(this);
    this.getData = this.getData.bind(this);
  }

  componentDidMount() {
    this.getData();
    var _this = this;
    $('input').iCheck({
      checkboxClass: 'icheckbox_flat-blue',
      radioClass: 'iradio_flat-blue'
    }).on('ifChecked', function (changeEvent) {
      _this.handleChange(changeEvent);
    });
  }

  handleChange(event) {
    var choiceId = event.target.dataset.choiceId;
    var val = event.target.value.toLowerCase();
    var redirect = '';
    if(val.indexOf('yes') >= 0) {
      redirect = '/thankyou'
    }

    this.setState({
      redirect: redirect,
      response: this.props.componentValue.choices[choiceId].value,
      option_id: this.props.componentValue.choices[choiceId].id
    }, () => {
      this.props.response(this.state);
    });
  }

  getData() {
    var _this = this;
    (new Connector).get('/api/quizzes/' + _this.props.quizzId + '/details' + '?player_id=' + localStorage.getItem('currentUser.id'), function(json) {
      _this.setState({quizz: json});
    });
  }

  renderImage() {
    if (this.props.question_image != null && !this.props.question_image.includes("no_image")) {
      console.log('aa');
      // return <img className="sr-slider__image" src={this.props.question_image;}/>
      return this.props.question_image;
    }
  }
  render() {
    var local = (new CurrentLocal).get()
    var questionsImage = [];
    var questionsInput = [];
    for (var i = 0; i <= this.props.componentValue.choices.length - 1; i++) {
      questionsImage.push(
        <label key={i} className="col-6">
          <img className='sr-question__radio_image' src={this.props.componentValue.choices[i].image} />
        </label>
      )
      questionsInput.push(
        <label key={i} className="col-6">
          <input className='sr-question__radio' type="radio" name="gender" data-choice-id={i} value={Translations.trans((this.props.translations ? this.props.translations[local.local] : ''), this.props.componentValue.choices[i].label)} />
          {Translations.trans((this.props.translations ? this.props.translations[local.local] : ''), this.props.componentValue.choices[i].label)}
        </label>
      )
    }

    return (
      <div className='container'>
        <div className='row'>
          <div className='col-12'>
            <div className='sr-question__container'>
              <div className="p-t-15  ">
                <div>
                  <text className="sr-question__text__title">{Translations.trans((this.props.translations ? this.props.translations[local.local] : ''), this.state.quizz.name)}</text>
                </div>
                <div>
                  <text className="sr-question__text__question">{Translations.trans((this.props.translations ? this.props.translations[local.local] : ''), this.state.quizz.description)}</text>
                </div>
              </div>

              <div>
                {this.renderImage()}
                <div>
                  <div>
                    <div>
                      <text className="sr-binary__container__title">{Translations.trans((this.props.translations ? this.props.translations[local.local] : ''), this.props.title)}</text>
                    </div>
                    <div className="p-l-15 p-r-15">
                      <text className="">{Translations.trans((this.props.translations ? this.props.translations[local.local] : ''), this.props.question)}</text>
                    </div>
                  </div>
                  <div style={{ "padding-bottom": "30px" }}>
                    <div className="question-bg" style={{ 'background': "url("+this.renderImage()+")", 'background-size': 'contain' }}>
                      {questionsImage}
                    </div>
                    <div>
                      {questionsInput}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Binary



